import { FullIdent } from 'web-ident';
import rtc from './p2p/rtc';
import { secure, authenticate } from './p2p/secure';
import sodium from 'libsodium-wrappers';

let inputData = Buffer.alloc(1e6);

sodium.ready.then(() => {
    let start = performance.now();

    let key = sodium.crypto_secretstream_xchacha20poly1305_keygen();
    let push = sodium.crypto_secretstream_xchacha20poly1305_init_push(key);

    let c1 = sodium.crypto_secretstream_xchacha20poly1305_push(push.state, inputData, null, 0);

    let pull = sodium.crypto_secretstream_xchacha20poly1305_init_pull(push.header, key);

    let p1 = sodium.crypto_secretstream_xchacha20poly1305_pull(pull, c1);
    let end = performance.now();

    console.log(key.length);
    console.log(end - start);

    run().catch(e => console.log(e.message));
});

async function run() {
    let start = performance.now();

    let key = await crypto.subtle.generateKey({
        name: 'AES-CTR',
        length: 256,
    }, false, ['encrypt', 'decrypt']);

    let nonce = Buffer.alloc(8);
    let counter = Buffer.alloc(8);

    await crypto.getRandomValues(nonce);

    let c1 = await crypto.subtle.encrypt({
        name: 'AES-CTR',
        counter: Buffer.concat([nonce, counter]),
        length: 64,
    }, key, inputData);

    let p1 = await crypto.subtle.decrypt({
        name: 'AES-CTR',
        counter: Buffer.concat([nonce, counter]),
        length: 64,
    }, key, c1);
    let end = performance.now();

    console.log(end - start);
}
